body,
html {
  font-family: "Roboto", "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  -webkit-font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
  color: #091e42;
}

a {
  color: #5465ff;
}

ul li {
  margin: 8px;
  line-height: 1.65;
}

b,
strong {
  font-weight: inherit;
  font-weight: bolder;
}

::selection {
  background: #d4d8ff;
  color: #333;
}

.img-res {
  max-width: 100%;
  height: auto;
}

p {
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -moz-font-smoothing: antialiased;
  text-rendering: optimizelegibility;
  letter-spacing: 0.03em;
}

h1 {
  font-size: 2rem;
  line-height: 1.23;
  font-weight: 200;
  letter-spacing: 0.01em;
}

@media (max-width: 599px) {
  h1 {
    font-size: 1rem;
  }
}

h2 {
  font-weight: 400;
}

h3 {
  font-weight: 400;
}

h4 {
  font-weight: 700;
}

h5 {
  font-weight: bold;
}

h6 {
  font-weight: bold;
}

p {
  font-weight: 400;
  font-size: 1rem;
  line-height: 1.9;
  margin: 10px 0 20px;
  letter-spacing: -0.01em;
}

blockquote p {
  margin-bottom: 10px;
}

blockquote footer {
  display: block;
  font-size: 80%;
  color: #6c757d;
}

blockquote footer::before {
  content: "\2014\00A0";
}

